import React from 'react'

const Contact = ({ logo, tagline }) => {
     
    return (
        <div>   
           <h2>{tagline}</h2>
            <div>
                <iframe title="contact form" src={"https://eepurl.com/dAlvpP"} />
            </div>
            

        </div>
    )
}

export default Contact


